<template>
  <div>

    <RedirectButton
      label="Return to Users"
      :show="done"
      uri="/admin/user/list"
    />

    <b-card id="user-delete" title="Delete User" v-if="validation">
      <p class="my-4">Delete this admin user?</p>
      <b-table
        stacked :items="[user]"
        :fields="['role', 'username', 'firstname', 'lastname']">
      </b-table>
      <b-card-footer align="center">
        <b-button
          :disabled="done"
          class="m-2"
          type="button"
          @click="remove()"
        >Delete</b-button>
        <b-button
          :disabled="done"
          class="m-2"
          type="button"
          variant="secondary"
          @click="reroute('/admin/user/list')"
        >Cancel</b-button>
      </b-card-footer>
    </b-card>
  </div>
</template>

<script>

import RedirectButton from '@/components/common/RedirectButton'
import formServices from '@/services/form.services'
import api from '@/services/api.services'

export default {
  name: 'user-delete',
  components: {
    RedirectButton
  },
  data () {
    return {
      user: {},
      done: false
    }
  },
  computed: {
    validation() {
      return !!this.user._id
    }
  },
  methods: {
    lookup(key, item) {
      return formServices.lookup(key, item)
    },
    async remove () {
      const guid = this.$route.params.guid || '';
      this.$store.dispatch('removeUser', guid)
        .catch(err => {
          console.error(err)
          this.$store.dispatch('handleError',
            {text: 'User could not be deleted.', type: 'danger'}
          )
        }).finally(()=>{this.done = true})
    },
    async reroute(uri) {
      await this.$router.push(uri)
    }
  },
  async beforeCreate() {
    await api.get(`users/view/${this.$route.params.guid}`)
      .then(response => {
        const {data = []} = response || {}
        return data
          .map(user => {
            const {
              _id = null,
              role = '',
              guid = '',
              username = '',
              firstname = '',
              lastname = '',
              email = '',
              createdAt = '',
              updatedAt = ''
            } = user || {}
            const updatedTS = new Date(updatedAt)
            const createdTS = new Date(createdAt)
            this.user = {
              id: _id,
              role: role,
              guid: guid,
              username: username,
              firstname: firstname,
              lastname: lastname,
              email: email,
              created: createdTS,
              updated: updatedTS,
            }
          })
      })
      .catch(err => {
        console.error(err)
        this.$store.dispatch('handleError',
          {text: 'User data failed to load.', type: 'danger'}
        )
      })
  }
}
</script>
